import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { FaFilter } from "react-icons/fa";
import { toast } from "react-toastify";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";

import roleAPI from "api/common/role";

export default function TableFilter({
  currentFilter,
  handleRoleFilterOnChange,
  handleIsActiveFilterOnChange,
  handleFilterOnClick,
}) {
  const { t } = useTranslation();

  const [roles, setRoles] = useState([]);
  const status = [
    {
      value: true,
      name: t("admin.user_list.ACTIVE"),
    },
    {
      value: false,
      name: t("admin.user_list.BLOCK"),
    },
  ];

  useEffect(async () => {
    try {
      const rolesRes = await roleAPI.getAll();
      setRoles(rolesRes.contents);
      // eslint-disable-next-line
    } catch (err) {
    }
  }, []);

  return (
    <Form className="d-flex flex-wrap">
      <FormGroup>
        <Label for="role">{t("admin.user_list.ROLE")}</Label>
        <Input
          type="select"
          value={currentFilter.role !== null ? currentFilter.role : -1}
          onChange={handleRoleFilterOnChange}
        >
          <option value={-1}>{t("admin.common.ALL")}</option>
          {roles &&
            roles.map((item, index) => {
              return (
                <option key={index} value={item.id}>
                  {item.name}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;
      <FormGroup>
        <Label for="status">{t("admin.user_list.STATUS")}</Label>
        <Input
          type="select"
          value={currentFilter.isActive !== null ? currentFilter.isActive : -1}
          onChange={handleIsActiveFilterOnChange}
        >
          <option value={-1}>{t("admin.common.ALL")}</option>
          {status &&
            status.map((item, index) => {
              return (
                <option key={index} value={item.value}>
                  {item.name}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;
      <FormGroup className="align-self-end">
        <Button onClick={handleFilterOnClick}>
          <FaFilter /> {t("admin.common.FILTER")}
        </Button>
      </FormGroup>
    </Form>
  );
}
