export function exportHead() {
  const head = [];
  head.push("Email");
  head.push("Username");
  head.push("First name");
  head.push("Last name");
  head.push("Role");
  head.push("Status");
  head.push("Available status");
  head.push("Created at");
  head.push("Updated at");
  return head;
}

export function exportRow(data) {
  const row = [];
  row.push(data.email);
  row.push(data.username);
  row.push(data.firstName);
  row.push(data.lastName);
  row.push(data.role.name);
  row.push(data.isActive ? "Active" : "Block");
  row.push(data.isDeleted ? "Deleted" : "Available");
  row.push(data.createdAt);
  row.push(data.updatedAt);
  return row;
}
